<template>
  <div id="app">
    <img src="./assets/logo.png">
    <h3><a href="https://github.com/nesfe/vue-weibo-modal" target="_blank">vue-weibo-modal</a></h3>
    <div>
      <div>
        <p>HTML生成：</p>
        <button class="ok" @click="modal1=!modal1">default</button>
        <button class="ok" @click="modal2=!modal2">default OPT</button>
      </div>
      <div>
        <p>代码生成：</p>
        <button class="ok" @click="open">$Modal</button>
        <button class="ok" @click="openOpt">$Modal OPT</button>
      </div>
    </div>
    <weibo-model v-model="modal1"></weibo-model>
    <weibo-model v-model="modal2">
      <template slot="title">HTML自定义标题</template>
      <template slot="btn-ok">确认按钮</template>
      <template slot="btn-cancel">取消按钮</template>
      <template slot="html">
        <div class="" style="text-align: center">
          <img
            src="data:image/png;base64,"
            alt="">
        </div>
      </template>
    </weibo-model>
  </div>
</template>

<script>

  export default {
    name: 'App',
    data () {
      return {
        modal1: false,
        modal2: false
      }
    },
    mounted () {
    },
    methods: {
      open () {
        this.$Modal('直接输入传参字符串的话就会直接显示')
        this.$Modal.info('直接输入传参字符串的话就会直接显示')
      },
      openOpt () {
        this.$Modal({
          title: '自定义标题',
          okText: '自定义确认按钮',
          html: '自定义内容 <p style="color: red">添加p元素</p><a href="#">a元素</a>',
          eventClose: () => {
            alert('点击的关闭')
          },
          eventOk: () => {
            alert('点击的确认')
          },
          eventCancel: () => {
            alert('点击的取消')
          },
          width: '90%'
        })
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>



